@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@700&display=swap'); /* 영문 폰트 */

* {
  font-family: 'Arial', 'NanumSquare';
  margin: 0;
}

#banner {
  font-family: "Noto Sans JP", "NanumSquare";

  background-color: #298dff;
  color: white;

  padding: 50px;
}
#banner_title {
  font-size: 4rem;
}

@media screen and (max-width: 400px) {
  #banner_title {
    font-size: 3rem;
  }
}